/**
 * Layout component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React from "react"
import { StaticQuery, graphql } from "gatsby"
import { Container, Row, Col, Image } from "react-bootstrap"
import Header from "./header"
import City from "./city"

const Layout = ({ children, pageInfo }) => (
  <StaticQuery
    query={graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            title
          }
        }
      }
    `}
    render={data => (
      <>
        <Container fluid className="px-0 main">
          <Row noGutters className="justify-content-center">
            <Col>
              <Header siteTitle={data.site.siteMetadata.title} />
            </Col>
          </Row>
          <Row noGutters>
            <Col>
              <section
                className="image-bg fullscreen overlay bg-bg"
                style={{
                  backgroundImage: 'url(https://s3.amazonaws.com/assets.ramraces.com/public/www.hotchocolate15k.com/assets/common/images/national-video-bg.jpg)'
                }}>
                <div className="fs-vid-background">
                  <video autoPlay muted loop>
                    <source src="https://s3.amazonaws.com/assets.ramraces.com/public/www.hotchocolate15k.com/assets/hot-chocolate/video/2015-hc-video-720p.webm" type="video/webm" />
                    <source src="https://s3.amazonaws.com/assets.ramraces.com/public/www.hotchocolate15k.com/assets/hot-chocolate/video/2015-hc-video-720p.mp4" type="video/mp4" />
                  </video>
                </div>
                <Container className="v-align-transform">
                  <Row>
                    <Col className="text-center" sm={{ span: 8, offset: 2 }}>
                      <Image
                          alt="Where Will You Run"
                          src="https://s3.amazonaws.com/assets.ramraces.com/public/www.hotchocolate15k.com/assets/hot-chocolate/images/where-will-you-run.png?1558973819"
                       />
                      <h4 className="lead mt40 mb20">Over one million people have joined the Hot Chocolate movement from coast to coast.</h4>
                      <City />
                    </Col>
                  </Row>
                </Container>
              </section>
            </Col>
          </Row>
          <Row noGutters>
            <Col>
              <Container>
                <main>{children}</main>
              </Container>
            </Col>
          </Row>
        </Container>
      </>
    )}
  />
)

export default Layout
